import React, { Component } from 'react';
import './footer.css';
import { FontAwesomeIcon } from '../../node_modules/@fortawesome/react-fontawesome';
import { faReact } from "@fortawesome/free-brands-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons";

const Footer = () => {
    return (
        <div className="Footer">
            <p className="Footer-SiteName">Quarantine Quarms</p>  
            <a href="https://github.com/" target="_blank"><FontAwesomeIcon icon={faGithub}/></a>
            <p className="Footer-DevBy">Developed by Daniel Jones using <FontAwesomeIcon icon={faReact}/>React</p>    
        </div>
    );
}

export default Footer;
