import React, { Component, useState } from 'react';
import './post.css';
import firebase from '../firebase.js';
import { faThumbsUp as fasThumbsUp } from "@fortawesome/free-solid-svg-icons";
import { faThumbsUp as farThumbsUp } from "@fortawesome/free-regular-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";


const Post = (props) => {

    const [clicked, setClicked] = useState(false);

    function iconClicked() {
        const postsRef = firebase.database().ref('posts');
        setClicked(!clicked);
        if (clicked) {
            //Reduce votes by 1
            postsRef.child(props.postId).update({votes: props.votes - 1});
        } else {
            //Increase votes by 1
            postsRef.child(props.postId).update({votes: props.votes + 1});
        }
    }

    return (
        <div className="Post">
            <button className="Post-Icon-Button" onClick={iconClicked}>
                <FontAwesomeIcon className={clicked ? "Post-Icon-Clicked": "Post-Icon"} icon={clicked ? fasThumbsUp : farThumbsUp}/>
            </button>
            <p className="Post-Vote-Count">{props.votes}</p>
            <p className="Post-Text">{props.text}</p>
        </div>
    );
}

export default Post;
