import React, { Component, useState } from 'react';
import './header.css';
import firebase from '../firebase.js';


const Header = () => {

    const [state, setState] = React.useState({
        inputString: ''
    });

    function handleClick() {
        const postsRef = firebase.database().ref('posts');
        const post = { votes: 0, text: state.inputString };
        postsRef.push(post);
        setState({inputString: ''});
    }

    return (
        <div className="Header">
            <h1 className="m-5">QUARANTINE QUARMS</h1>
            <h3 className="m-4">Tell us about your quarantine</h3>
            <textarea id="Header-Input" 
                type="text" 
                value={state.inputString} 
                onChange={event => setState({inputString: event.target.value})}
                placeholder="What's the worst part of quarantine? What is the best part of quarantine? 
                Which one of your family members do you want to kill and why?" />
            <button id="Header-Input-Button" onClick={handleClick}>POST</button>
        </div>
    );
}

export default Header;
